  ShowModalDataHadiah: false,
  ShowModalParameterRugiNota: false,
  ShowModalGroup: false,
  GetDataGroup: [],
  getDataJenis: [],
  ShowModalDataKadar: false,
  getDataKadar: false,
  getDataGudang: [],
  getDataBaki: [],
